.parallax-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
}
.parallax-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
}
/* GRASS */
div#parallax-layer-1 {
  z-index: -1;
}
/* WATER */
div#parallax-layer-2 {
  z-index: -2;
  overflow: hidden;
  position: absolute;
  width: 200%;
  height: 100%;
  background-image: inherit;
  background-repeat: repeat-x;
  background-size: 50% 100%;
  background-position-x: -200%;
  animation: water 30s linear infinite;
  will-change: transform;
}
/* SKY */
div#parallax-layer-3 {
  z-index: -3;
  overflow: hidden;
  position: absolute;
  width: 200%;
  height: 100%;
  background-image: inherit;
  background-repeat: repeat-x;
  background-size: 50% 100%;
  animation: wind 90s linear infinite;
  will-change: transform;
}
@keyframes water {
  0% {
    transform: translateX(-50%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes wind {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}
@media screen and (max-width: 768px) {
  .parallax-layer {
    background-position: top center !important;
  }
  /* WATER & SKY */
  div#parallax-layer-2,
  div#parallax-layer-3 {
    width: 100% !important;
    background-size: cover !important;
    animation: none !important;
  }
}
